*{
  margin: 0;
}

.app{
  background-color: #fafafa;
  height: 100vh;
}
.app__header{
  object-fit: contain;
  padding: 20px;
  background-color: white;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
}
.app__headerImage{
  object-fit: contain;
}
.app__signUp{
  display: flex;
  flex-direction: column;
  text-align: center;
}

.app__posts{
  padding: 20px;
  display: flex;
  justify-content: center;
}

.app__postsRight{
  margin-left: 20px;
}